import { reactive, toRefs } from 'vue'
import { getTopicList, ListResponse, getCourseList } from '@/api'
import bannerList from '@/mock/banner'
export type BannerResponse = {
  id: string
  img: string
  type: number
}[]
export interface IndexInfoType {
  bannerList: BannerResponse
  topicList: ListResponse
  cardList: ListResponse
}
export const indexHooks = () => {
  const info = reactive<IndexInfoType>({
    bannerList: [],
    topicList: [],
    cardList: []
  })
  const requestBanner = async () => {
    info.bannerList = bannerList
  }

  const requestTopic = async () => {
    const res = await getTopicList({})
    info.topicList = res.list
  }

  const requestCard = async () => {
    const res = await getCourseList({
      topicId: 'a3038c51606f4ef695beb0f4ca507ab1',
      pageNum: 1,
      pageSize: 20
    })
    const res2 = await getCourseList({
      topicId: '828c795a34f541a295ee129e41deeb5b',
      pageNum: 1,
      pageSize: 20
    })
    info.cardList = res.list
    info.cardList = info.cardList.concat(res2.list)
  }

  return { ...toRefs(info), requestBanner, requestTopic, requestCard }
}
